/* blog-detail
----------------------------------------- */
@use "../../global" as *;

body.blog-detail {
    .c-breadcrumbs {
        margin-top: rem(60);

        @include responsive(md) {
            margin-top: rem(80);
        }

        @include responsive(lg) {
            margin-top: rem(60);
        }

        @include responsive(lg2) {
            margin-top: rem(118);
        }
    }
}

/*==============================================================
.p--blog-detail
==============================================================*/
.p-blog-detail {
    background: $color-background;

    &__body {
        background: #fff;
        border-radius: 10px;
        padding: rem(30) rem(20);

        @include responsive(md) {
            padding: rem(41) rem(40) rem(23);
        }
    }
    &__index {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        padding-left: rem(18);
        position: relative;

        @include responsive(lg2) {
            font-size: rem(14);
        }

        &::before {
            content: "";
            width: rem(12);
            height: rem(14);
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            background: url("../images/home/blog_icon.svg");
        }
    }

    &__day {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
    }

    &__txt {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(21 / 14);

        @include responsive(lg2) {
            font-size: rem(18);
        }
    }

    &__intro {
    }

    &__img {
        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__introText {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 14);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(28 / 16);
        }
    }

    &__content {
    }

    &__contentList {
    }

    &__contentItem {
    }

    &__contentTitle {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(24 / 24);
        }
    }

    &__contentText {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 14);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(28 / 16);
        }
    }
}

/*==============================================================
.p--blog-detail__nav
==============================================================*/
.p-blog-detail {
    &__nav {
        background: #f0f0f0;
    }

    &__navList {
        @include responsive(md) {
            display: flex;
            justify-content: space-around;
            align-items: center;
        }
    }

    &__navItem {
        position: relative;
    }

    &__navItem.-prev::before {
        position: absolute;
        content: "";
        width: rem(24);
        height: rem(24);
        left: 0;
        top: 0;
        background: url(../images/blog/details/arrow02-left.svg);
    }

    &__navItem.-next::before {
        position: absolute;
        content: "";
        width: rem(24);
        height: rem(24);
        right: 0;
        top: 0;
        background: url(../images/blog/details/arrow02-right.svg);
    }

    &__navText {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(16 / 16);
        }
    }
    &__navDay {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
    }
}
